.header {
  background-color: #ffffff;
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
  border-bottom: 1px solid rgb(240, 240, 240);
  padding: 13px 0 13px 0;

  box-shadow: 4px 8px 66px -16px rgba(0, 0, 0, 0.58);
  -webkit-box-shadow: 4px 8px 66px -16px rgba(0, 0, 0, 0.58);
  -moz-box-shadow: 4px 8px 66px -16px rgba(0, 0, 0, 0.58);
}
